export default {
	onLoad() {
		console.log('-=-=-=-=-=-')
		console.log(this.CustomBar * 1)
		uni.getSystemInfo({
			success: function(e) {
				console.log(e)
			}
		})
	},
	data() {
		return {
			screenHeight: this.screenHeight,
			statusBarHeight : this.statusBarHeight ,
			phone:'',
			password:''
		}
	},
	computed: {
		style() {
			let style = `height:${this.screenHeight - this.statusBarHeight}px; `;
			return style;
		},
	},
	methods:{
		denglusss:function(){
			this.$https({url:'/api/oauth/phoneLogin',data:{phone:this.phone,password:this.password},dengl:true,method:'post',success:function(res){
				if(res.data.data){
					uni.setStorageSync('Authorization',res.data.data.access_token)
					uni.showToast({
						title:'登录成功'
					})
					setTimeout(function(){
						uni.navigateTo({
							url:'../index/index'
						})
					},1900)
				}else{
					uni.showToast({
						title:res.data.message
					})
				}
			}})
		},
		//跳转到注册页面
		login:function(){
			uni.navigateTo({
				url:'login'
			})
		},
		//跳转到修改密码
		xiuga:function(){
			uni.navigateTo({
				url:'../user/alter/alterPassword/alterPassword'
			})
		}
	}
}
